.contentList-root {
    width: 100%;
    height: 100%;
    background: #fff;
    position: relative;
    padding: 10px 20px;
    background: #f3f3f3;

    .card-list {
        background: #fff;
        margin-bottom: 10px;

        .card-title {
            padding: 10px;
            border-bottom: 1px solid #ccc;
            color: #959292;

            span {
                color: #000;
            }
        }

        .cart-content {
            padding: 10px;

            & > div {
                margin: 15px 0;
                color: #959292;
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;

                img {
                    width: 30px;
                    height: 30px;
                    vertical-align: text-bottom;
                    margin-right: 10px;
                }

                span {
                    color: #000;
                    margin-left: 15px;
                }
            }
        }
    }

}