.EmergencySituation-root {
    width: 100%;
    height: 100%;
    background: #fff;
    position: relative;
    padding: 10px 20px;
    background: #f3f3f3;

    .card-list {
        height: 220px;
        background: #fff;
        margin-bottom: 10px;
        display: flex;

        .card-left {
            padding: 0 10px;
            width: 80%;

            & > div {
                margin: 15px 0;
                color: #959292;
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;

                img {
                    width: 30px;
                    height: 30px;
                    vertical-align: text-bottom;
                    margin-right: 10px;
                }

                span {
                    color: #000;
                    margin-left: 15px;
                }
            }
        }

        .cart-right {
            width: 20%;
            height: 100%;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            padding: 10px 0 20px;

            & > div:nth-of-type(1) {
                text-align: right;
                margin-right: 10px;
            }

            & > div:nth-of-type(2) {
                display: flex;
                justify-content: space-between;

                & > div:nth-of-type(1) {
                    width: 60%;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    white-space: nowrap;
                }

                img {
                    width: 30px;
                    height: 30px;
                    vertical-align: text-bottom;
                    margin-right: 10px;
                }
            }
        }
    }



}