.no-wrap {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  word-break: break-all;
}
.extend-click {
  position: relative;
}
.extend-click:before {
  content: '' !important;
  position: absolute;
  top: -10px;
  left: -10px;
  right: -10px;
  bottom: -10px;
}
.clearfix {
  display: inline-block;
}
.clearfix:after {
  display: block;
  content: '' !important;
  height: 0;
  line-height: 0;
  clear: both;
  visibility: hidden;
}
.position-full {
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.flex-center {
  display: flex;
  align-items: center;
  justify-content: center;
}
.eventEditing-root {
  width: 100%;
  height: 100%;
  height: 100vh;
  position: relative;
  overflow: hidden;
  background-color: #f7f8fa;
}
.eventEditing-root .sup-navbar-center {
  font-size: 1rem;
}
.eventEditing-root .mask {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.55);
  z-index: 2;
}
.eventEditing-root .sup-navbar {
  position: relative;
}
.eventEditing-root .sup-navbar .fillRfresh {
  margin-right: 20px;
  font-size: 40px;
  margin-top: 6px;
  width: 5vw;
  height: 5vh;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.eventEditing-root .sup-navbar .fillRfresh span {
  width: 20%;
  height: 20%;
  border-radius: 50%;
  background: #fff;
}
.eventEditing-root .sup-navbar .list {
  width: 30vw;
  height: auto;
  position: absolute;
  right: 2vw;
  border-radius: 4px;
  margin-top: 1.5vh;
  color: #000;
  font-size: 12px;
  z-index: 3;
  background-color: #fff;
}
.eventEditing-root .sup-navbar .list .list-btn {
  height: 6vh;
  width: 90%;
  margin: 0 auto;
  border-bottom: 1px solid #eeeeee;
  display: flex;
  align-items: center;
  padding-left: 2vw;
}
.eventEditing-root .sup-navbar .list .list-btn .icon {
  width: 40px;
  height: 40px;
}
.eventEditing-root .sup-navbar .list .list-btn .btn {
  padding-left: 2vw;
  font-size: 0.8rem;
  font-weight: 600;
}
.eventEditing-root .sup-navbar .list .list-btn:last-child .btn {
  border: none;
}
.eventEditing-root .tabs {
  display: flex;
  height: 100%;
  align-items: center;
  justify-content: center;
  background-color: #fff;
}
