.basic-info-root {
    width: 100%;
    height: 100%;
    background: #fff;
    position: relative;
    padding: 20px;

    & > div {
        display: flex;
        justify-content: space-between;
        border-bottom: 1px solid #ccc;
        padding: 10px 0;

        & > div:nth-of-type(1) {
            min-width: 20%;
            color: rgb(149, 146, 146);
        }
    }
}